import React, { useContext, useEffect, useState } from 'react'
import { CartContext } from './Context'
import CartItem from './CartItem';
import './Cart.css'
import 'bootstrap/dist/css/bootstrap.css';
import Swal from 'sweetalert2'


export default function Cart() {
  const context = useContext(CartContext);
  const [items, setItems] = useState(context[2]);
  const [quantity, setQuantity] = useState(context[2]);
  // remove-     :
  function decrementitem() {
    setItems(items - 1)
  }
  function quantityplus() {
    setQuantity(quantity + 1)
  }
  function quantityminus() {
    setQuantity(quantity - 1)
  }
  function clear() {
    let d=[];
    d.push(context[1].map((item)=>{item.status='Add to cart'; return item}))
    context[0].clearCart(d)
  }

 
    return (
      <>
      <div className='cartHead'>
        <p>Total Item: {items}</p>
        <p>Total Quantity: {quantity} </p>
        <p>Total Price: {context[4]} $</p>
        <button className='makeOrder' onClick={() => {
          Swal.fire(
            'Good job!',
            'You clicked the button!',
            'success'
          )
        }}>Make Order</button>
          <button className='clearCart' onClick={() => { clear(); context[0].setCounter(0); setItems(0); context[0].decrementTotal(context[4]); setQuantity(0)}}>Clear Cart</button>
        </div>

        <div className='container-fluid'>
          <div className='row' style={{ width: '100%', justifyContent: 'center' }}>
            {context[1].map((item, id) => {
              return <CartItem item={item} id={id} onClick={decrementitem} funqcia={quantityplus} funqcia1={quantityminus} />
            })}
          </div>
        </div>
      </>
    )
  }

