import React, { useState, useContext } from 'react'
import { CartContext } from './Context'
import { Link } from 'react-router-dom'
import { useRef } from 'react'
import './SingleCard.css'
import 'bootstrap/dist/css/bootstrap.css';


export default function SingleCard(props) {
    let ref = useRef();
    const context = useContext(CartContext);
    return (
        <div className='col-lg-2 border m-5' id='SingleCard' key={props.id}>
            <img className='dish' src={props.item.image} />
            <h1 className='SingleCardH1'>{props.item.name}</h1>
            <div className='details_price'><Link to={`/restaurant-ReactAPP/menu/${props.item.id}`}><button className='details_price_btn'>DETAILS</button></Link><p className='SingleCardP'>Price: <span className='hf'>{props.item.price} $</span></p> </div>
            <button className='singlCardBtn' onClick={(e) => { context[0].increment(e); props.item.status = "Added"; context[0].incrementTotal(props.item.price); ref.current.disabled = true }}>{props.item.status}</button>
        </div>
    )
}