import React, { useState, useContext, useEffect } from 'react'
import { CartContext } from './Context'
import './CartItem.css'
import 'bootstrap/dist/css/bootstrap.css'


export default function CartItem(props) {
    const [quantity, setQuantity] = useState(1)
    const context = useContext(CartContext);

    if(props.item.quantity===undefined){
        props.item.quantity = quantity
    }

    
   
    if (props.item.status === "Added") {
        return (
            <div className='col-lg-2 border m-5' id='cartitem' key={props.id}>
            <div className='imgBox'>
                <img className='img' src={props.item.image} />
                    <button onClick={() => { context[0].decrement(); context[0].decrementTotal(props.item.price * props.item.quantity); props.item.status = 'Add to cart'; props.onClick()}} className='remove_to_cart'>remove to cart</button>
            </div>
            <h1 className='cartitemH1'>{props.item.name}</h1>
            <div className='price'>
                <p>Price: <span>{props.item.price} $</span></p>
                <p>Total Price: <span>{props.item.quantity * props.item.price}</span> $</p>
            </div>
            <div className='quantity'>
                    <button className='quantity_button1' onClick={() => { quantity > 1 ? setQuantity(quantity - 1) : setQuantity(quantity=1); props.item.quantity -= 1; props.funqcia1(); context[0].decrementTotal(props.item.price) }}>-</button>
                <p className='quantityP'>Quantity: <span>{props.item.quantity}</span></p>
                    <button className='quantity_button' onClick={() => {setQuantity(quantity + 1); props.item.quantity += 1; props.funqcia(); context[0].incrementTotal(props.item.price)}}>+</button>
            </div>
        </div>
        )
    }
}
