import React, { useContext } from 'react'
import './Header.css';
import { NavLink, Outlet } from 'react-router-dom'
import 'bootstrap/dist/css/bootstrap.css';
import Logo from '../image/logo.png'
import { CartContext } from './Context'


export default function Header() {
    const context = useContext(CartContext);
    return (
        <>
            <header>
                <nav>
                    <ul>
                        <NavLink to='/restaurant-ReactAPP/'><li><img className='restaurant_logo' src={Logo} alt="restaurant logo" /></li></NavLink>
                        <NavLink to='/restaurant-ReactAPP/'><li className='test'>HOME</li></NavLink>
                        <NavLink to='/restaurant-ReactAPP/about'><li className='test'>ABOUT</li></NavLink>
                        <NavLink to='/restaurant-ReactAPP/contact'><li className='test'>CONTACT</li></NavLink>
                        <NavLink to='/restaurant-ReactAPP/menu'><li className='test'>MENU</li></NavLink>
                        <div style={{ position: 'relative' }}>
                            <NavLink to='/restaurant-ReactAPP/cart'><li><img className='cart_logo' src="https://www.rawshorts.com/freeicons/wp-content/uploads/2017/01/orange_shoppictcart_1484336529.png" alt="Shopping Cart logo" />
                            </li></NavLink>
                            <div style={{color:'yellow', position:'absolute', top:'2px', right:'-15px', fontSize:'25px', backgroundColor:'coral', borderRadius:'50%', width:'30px', height:'30px', textAlign:'center'}}>{context[2]}</div>
                        </div>
                    </ul>
                </nav>
            </header>
            <Outlet />
        </>
    )
}
