import React, { useEffect, useState } from 'react'
import './Slider.css'


let backgroundImage = [
    "https://imagesvc.meredithcorp.io/v3/mm/image?url=https%3A%2F%2Fstatic.onecms.io%2Fwp-content%2Fuploads%2Fsites%2F9%2F2022%2F05%2F16%2FPina-Colada-Pie-FT-2-RECIPE0622.jpg",
    "https://www.expatica.com/app/uploads/sites/2/2021/05/leche-frita.jpg",
    "https://elle.in/wp-content/uploads/2022/05/feature-54.jpg",
    "https://foodfornet.com/wp-content/uploads/White-chocolate-martini-800x534.jpg"
]

export default function Slider() {
    let i = 0;
    const [image, setImage] = useState("url(" + backgroundImage[i] + ")")
    useEffect(() => {
        let timer = setInterval(() => {
            setImage("url(" + backgroundImage[i] + ")")
            if (i === 3) {
                i = 0
            } else {
                i++
            }
        }, 2000)
        return (() => {
            clearInterval(timer)
        })
    }, [])
    return (
        <>
            <div className='slider' style={{ backgroundImage: image }} className='slider'></div>
        </>

    )
}
