import React, { useContext, useEffect } from 'react'
import 'bootstrap/dist/css/bootstrap.css';
import { Dropdown } from 'react-bootstrap'
import { useState } from 'react'
import SingleCard from './SingleCard'
import './Menu.css'
import Footer from './Footer'
let Data = require('../data.json')

export default function Menu() {
  let [done, setDone] = useState('All')
  return (
    <>

      <Dropdown className='menu'>
        <Dropdown.Toggle variant="success" id="dropdown_basic">
          {done}
        </Dropdown.Toggle>

        <Dropdown.Menu className='down_menu'>
          <Dropdown.Item href="#/action-1" className='item' onClick={() => { setDone("All") }}>All</Dropdown.Item>
          <Dropdown.Item href="#/action-1" className='item' onClick={() => { setDone("Main Dish") }}>Main Dish</Dropdown.Item>
          <Dropdown.Item href="#/action-2" className='item' onClick={() => { setDone("Side Dish") }}>Side Dish</Dropdown.Item>
          <Dropdown.Item href="#/action-3" className='item' onClick={() => { setDone("Drinks") }}>Drinks</Dropdown.Item>
          <Dropdown.Item href="#/action-3" className='item' onClick={() => { setDone("Dessert") }}>Dessert</Dropdown.Item>
        </Dropdown.Menu>
      </Dropdown>

      <div className='container-fluid'>
        <div className='row' style={{width:'100%', justifyContent:'center'}}>
          {Data.map((item) => {
            if (done === 'All') {
              return (
                <SingleCard item={item}  />
              )
            } 
              if (item.category === done) {
                return (<SingleCard item={item}  />)
              }
            
          })}
        </div>
      </div>
      <Footer />
    </>
  )
}
