import React from 'react'
import './Footer.css'
import facebook from '../image/fb.png'
import mail from '../image/mail.png'
import instagram from '../image/instagram.png'
import { Link } from 'react-router-dom'

export default function Footer() {
  return (
    <div className='footer'>
        <div className='footer_icone'>
       
        <a href="#"><div className='iconBorder'><img src={facebook} alt="facebook log" /></div></a>
        <a href="#"><div className='iconBorder'><img src={mail} alt="mail log" /></div></a>
        <a href="#"><div className='iconBorder'><img src={instagram} alt="instagram log" /></div></a>
        
        </div>
    </div>
  )
}
