import React from 'react'
import contact from '../image/contact.png'
import location from '../image/location.png'
import phone from '../image/phone.png'
import email from '../image/email.png'
import Footer from './Footer'
import './Contact.css'


export default function Contact() {
  return (
    <>
    <div className='container_contact'>
      <div className='contact_title'>
        <img style={{width:'50px', height:'50px'}} src={contact} alt="contact us icone" />
        <h1>CONTACT US</h1>
      </div>
      <div className='contact'>
      <div className='info_box'>
      <div className='info'>
        <img style={{ width: '50px', height: '50px' }} src={location} alt="location icone" />
            <p>Rustaveli Avenue</p>
      </div>
      <div className='info'>
        <img style={{ width: '50px', height: '50px' }}  src={phone} alt="phone icone" />
        <a href="tel:+995 32 222 222">995 32 222 222</a>
      </div>
      <div className='info'>
        <img style={{ width: '50px', height: '50px' }}  src={email} alt="email icone" />
        <a href="mailto:mail@gmail.com?subject=The%20subject%20of%20the%20mail">mail@gmail.com</a>
      </div>
      </div>
      <iframe className='map' src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2978.7584806467285!2d44.787354515285465!3d41.704149284261234!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x40440cdb00b97a89%3A0x31137c1f7bbbd2ff!2sRustaveli!5e0!3m2!1sen!2sge!4v1653730077496!5m2!1sen!2sge"></iframe>
        
    </div>
        <Footer />
    </div>
    </>
  )
}



